.App {
  text-align: center;
  background-color: #282c34;
  height: 200vh;
  max-width: 600px;
  margin: auto;
  position: relative;
  overflow: hidden;
}
* {
  box-sizing: border-box;
}

.last-card,
.card {
  border: 2px solid;
  padding: 15px;
  font-size: 20vw;
  background: grey;
  display: inline-block;
  width: 90vw;
}

.last-card {
  font-size: 10vw;
  opacity: 0.5;
  width: 60vw;
}
.last-result {
  font-size: 20px;
}

.App-link {
  color: #61dafb;
}
.buttons {
  margin-top: 10vh;
}
.buttons span {
  font-size: 20vw;
  margin: 10px;
  cursor: pointer;
}

.animation-delay-200 {
  -webkit-animation-delay: 0.2s;
  animation-delay: 0.2s;
}

@media screen and (min-width: 600px) {
  .buttons span {
    font-size: 120px;
  }
  .last-card,
  .card {
    font-size: 120px;
    width: 600px;
  }
  .last-card {
    font-size: 60px;
    width: 400px;
  }
}
